<template>
    <div class="sensi-opera-log">
        <i-table :columns="sensiOperaLogCols" height="600" :data="sensiOperaLogData"></i-table>
        <Page class="footer-page" :total="page.total" :page-size="page.pageSize" :current="page.pageNum" show-sizer @on-page-size-change="handleChangeSize" @on-change="handleChangePage"/>
        <loading :loading="isLoading"></loading>
    </div>
</template>

<script>
export default {
    data() {
        return {
            page: {
                total: 0,
                pageSize: 10,
                pageNum: 1
            },
            sensiOperaLogCols: [
                {
                    title: '类型',
                    align: 'center',
                    key: 'type'
                },
                {
                    title: '操作位置',
                    align: 'center',
                    key: 'operaPosition'
                },
                {
                    title: '操作前内容',
                    align: 'center',
                    render: (h, params) => {
                        return h('textarea', {
                            style: {
                                height: '80px',
                                border: 'none',
                                backgroundColor: '#fff'
                            },
                            attrs: {
                                disabled: true
                            }
                        }, params.row.beforeOpera)
                    }
                },
                {
                    title: '操作后内容',
                    align: 'center',
                    render: (h, params) => {
                        return h('textarea', {
                            style: {
                                height: '80px',
                                border: 'none',
                                backgroundColor: '#fff'
                            },
                            attrs: {
                                disabled: true
                            }
                        }, params.row.afterOpera)
                    }
                },
                {
                    title: '操作时间',
                    align: 'center',
                    key: 'operaTime'
                }
            ],
            sensiOperaLogData: [],
            isLoading: false
        }
    },
    mounted() {
        let that = this;

        that.getData();
    },
    methods: {
        handleChangeSize(_pageSize) {
            let that = this;

            that.page.pageSize = _pageSize;
            that.page.pageNum = 1;
            that.getData();
        },
        handleChangePage(page) {
            let that = this;

            that.page.pageNum = page;
            that.getData();
        },
        getData() {
            let that = this,
                param = {};

            param['start'] = that.page.pageNum;
            param['pageSize'] = that.page.pageSize;

            if (!that.isLoading) {
                that.isLoading = true;

                that.$axios({
                    url: '/api/Setup/GetSensitiveInfos',
                    params: param
                }).then(function(data) {
                    if (data.data.Status === 100) {
                        const result = data.data.Data;

                        that.page.total = data.data.Total;
                        that.sensiOperaLogData = result.map(item => {
                            return {
                                id: item.ID,
                                type: item.Type,
                                operaPosition: item.Location,
                                beforeOpera: item.OpAcontent,
                                afterOpera: item.OpBcontent,
                                operaTime: item.CreateTime
                            }
                        })
                    }
                    that.isLoading = false;
                })
            }
        }
    },
}
</script>

<style lang="scss" scoped>
.sensi-opera-log {
    position: relative;

    padding: 15px 10px;

    border-radius: 5px;
    background-color: rgb(242, 242, 242);
    border: 1px solid rgb(215, 215, 215);
}
</style>